import React, { useState, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Typography,
  CardMedia,
  Paper,
  IconButton,
  InputBase,
} from "@material-ui/core/";
import SearchIcon from "@material-ui/icons/Search";
import { useSpring, animated } from "react-spring";

const api = {
  key: "3e24f8ec3ab92f67d68bd5902b5bd088",
  base: "https://api.openweathermap.org/data/2.5/weather?q=",
};

const useStyles = makeStyles((theme) => ({
  app: {
    minHeight: "-webkit-fill-available",
    width: "100vw",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-evenly",
    alignItems: "center",
    textAlign: "center",
    background:
      "radial-gradient(circle, rgba(27,131,255,1) 0%, rgba(23,134,255,1) 60%, rgba(115,184,255,1) 100%)",
  },
  icon: {
    height: 250,
    width: 250,
    margin: "-25px 0 -50px 0",
  },
  text: {
    color: "white",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 1)",
  },
  time: {
    color: "white",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 1)",
    fontSize: "3rem",
  },
  temp: {
    color: "white",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 1)",
    fontSize: "6rem",
    marginRight: "-1.5rem",
  },
  search: {
    color: "rgba(0, 0, 0, 0.8)",
    backgroundColor: "rgba(255, 255, 255, 0.5)",
    fontSize: "2rem",
    alignSelf: "center",
  },
  paper: {
    backgroundColor: "rgba(255, 255, 255, 0)",
    display: "flex",
    maxWidth: 300,
  },
  button: {
    color: "rgba(0, 0, 0, 0.8)",
    alignSelf: "center",
  },
}));

function App() {
  const [query, setQuery] = useState("");
  const [weather, setWeather] = useState({});
  const [height, setHeight] = useState(window.innerHeight);
  const [anime, setAnimated] = useState(false);

  window.addEventListener("resize", () => {
    setHeight(window.innerHeight);
  });

  let divHeightStyle = {
    height: height,
  };

  const classes = useStyles();

  const search = () => {
    fetch(`${api.base}${query}&units=metric&lang=ru&appid=${api.key}`)
      .then((res) => res.json())
      .then((result) => {
        setWeather(result);
        setQuery("");
        console.log(result);
      })
      .catch((e) => {
        console.log(e);
      });
    setAnimated(true);
  };

  const animate = () => {
    setAnimated(false);
    setTimeout(() => {
      search();
    }, 500);
  };

  let fromOutsideNumber = -100 - window.innerWidth / 2;

  const slide1 = useSpring({
    config: { mass: 3 },
    transform: anime
      ? "translate3d(0,0,0)"
      : `translate3d(${fromOutsideNumber}px,0,0)`,
  });
  const slide2 = useSpring({
    config: { mass: 3 },
    transform: anime
      ? "translate3d(0,0,0)"
      : `translate3d(${-fromOutsideNumber}px,0,0)`,
  });
  const dateSlide = useSpring({
    transform:
      typeof weather.main != "undefined"
        ? "translate3d(0,0,0)"
        : "translate3d(0,50%,0)",
  });
  const searchSlide = useSpring({
    transform:
      typeof weather.main != "undefined"
        ? "translate3d(0,0,0)"
        : "translate3d(0,-100%,0)",
  });
  const cityTransition = useSpring({
    opacity: anime ? 1 : 0,
  });

  const days = [
    "Воскресенье",
    "Понедельник",
    "Вторник",
    "Среда",
    "Четверг",
    "Пятница",
    "Суббота",
  ];
  const months = [
    "Января",
    "Февраля",
    "Марта",
    "Апреля",
    "Мая",
    "Июня",
    "Июля",
    "Августа",
    "Сентября",
    "Октября",
    "Ноября",
    "Декабря",
  ];

  const day = days[new Date().getDay()];
  const date = new Date().getDate();
  const month = months[new Date().getMonth()];
  const minutes = () => {
    if (new Date().getMinutes() < 10) {
      return "0" + new Date().getMinutes();
    } else {
      return new Date().getMinutes();
    }
  };
  const time = `${new Date().getHours()}:${minutes()}`;

  return (
    <div className={classes.app} style={divHeightStyle}>
      <animated.div style={dateSlide}>
        <Typography
          className={classes.text}
        >{`${day}, ${date} ${month}`}</Typography>
        <Typography className={classes.time}>{time}</Typography>
      </animated.div>
      {typeof weather.main != "undefined" ? (
        <div>
          <animated.div style={cityTransition}>
            <Typography className={classes.text}>{weather.name}</Typography>

            <Typography className={classes.text}>
              {weather.weather[0].description.charAt(0).toUpperCase() +
                weather.weather[0].description.slice(1)}
            </Typography>
          </animated.div>
          <animated.div style={slide1}>
            <CardMedia
              image={`http://openweathermap.org/img/wn/${weather.weather[0].icon}@4x.png`}
              className={classes.icon}
            ></CardMedia>
          </animated.div>
          <animated.div style={slide2}>
            <Typography className={classes.temp}>
              {Math.round(weather.main.temp)}°
            </Typography>
          </animated.div>
        </div>
      ) : (
        <div></div>
      )}

      <animated.div style={searchSlide}>
        <Paper className={classes.paper} elevation={3}>
          <InputBase
            variant="filled"
            className={classes.search}
            onChange={(e) => setQuery(e.target.value)}
            value={query}
            onKeyPress={(e) => e.key === "Enter" && animate()}
          />
          <IconButton
            type="submit"
            onClick={animate}
            className={classes.button}
          >
            <SearchIcon />
          </IconButton>
        </Paper>
      </animated.div>
    </div>
  );
}

export default App;
